<template>
  <nav class="navMenu">
    <ul class="navMenu_wrapper">
      <li v-for="item in this.links" :key="item.key" class="navMenu_wrapper_item" :class="{'navMenu_wrapper_item-Active': item.key === active}">
        <a @click.prevent="scrollToParagraph(item.value)" href="#">
          <span class="navMenu_wrapper_item-line"/>
          <span class="navMenu_wrapper_item-text">{{item.value}}</span>
        </a>
      </li>
    </ul>
  </nav>
</template>

<script>
export default {
  data () {
    return {
      links: [
        { key: 1, value: 'Обо Мне' },
        { key: 2, value: 'Опыт' },
        { key: 3, value: 'Проекты' }
      ],
      active: 1
    }
  },
  methods: {
    scrollToParagraph (key) {
      const paragraphElement = document.getElementById('paragraph')
      const experienceElement = document.getElementById('experience')
      const projectElement = document.getElementById('project')
      switch (key) {
        case 'Обо Мне':
          this.active = 1
          paragraphElement.scrollIntoView({ behavior: 'smooth', block: 'end' })
          break
        case 'Опыт':
          this.active = 2
          experienceElement.scrollIntoView({ behavior: 'smooth', block: 'start' })
          break
        case 'Проекты':
          this.active = 3
          projectElement.scrollIntoView({ behavior: 'smooth', block: 'start' })
          break
      }
    },
    autoSwap () {
      const experienceElement = document.getElementById('experience')
      const projectElement = document.getElementById('project')
      window.addEventListener('scroll', (e) => {
        if (experienceElement.getBoundingClientRect().top > 100) {
          this.active = 1
        } else if (experienceElement.getBoundingClientRect().top < 100) {
          this.active = 2
        }
        if (projectElement.getBoundingClientRect().top < 100) {
          this.active = 3
        }
      })
    }
  },
  mounted () {
    this.autoSwap()
  }
}
</script>
