<template>
  <div id="cursorEffect" :style="{background: `radial-gradient(600px at ${this.left}px ${this.top}px, rgba(29, 78, 216, 0.15), transparent 80%)`}"/>
  <div class="container">
    <div class="wrapper">
      <div class="main_header">
        <div>
          <titleAndSubtitle/>
          <navBar/>
        </div>
        <socialNav/>
      </div>
      <div class="main_page">
        <mainContent/>
      </div>
    </div>
  </div>
</template>

<script>
import titleAndSubtitle from '@/components/titleAndSubtitle.vue'
import navBar from '@/components/navBar.vue'
import socialNav from '@/components/socialNav.vue'
import mainContent from '@/components/mainContent.vue'
export default {
  components: {
    titleAndSubtitle,
    navBar,
    socialNav,
    mainContent
  },
  data () {
    return {
      left: 0,
      top: 0,
      width: 0
    }
  },
  methods: {
    mouseEffect () {
      const events = (event) => {
        this.left = event.pageX
        this.top = event.pageY
      }
      window.addEventListener('mousemove', events)
    }
  },
  mounted () {
    this.mouseEffect()
  }
}
</script>
